@mixin setFont($alias, $name, $weight: 'normal', $style: 'normal') {
  @font-face {
    font-family: $alias;
    src: url("/assets/fonts/" + $name + ".woff2") format("woff2"), url("/assets/fonts/" + $name + ".ttf") format("ttf");
    font-weight: $weight;
    font-style: $style;
    font-display: swap; } }

@include setFont('Amatic', 'Amatic-Regular', 400);
@include setFont('Amatic', 'Amatic-Bold', 700);
@include setFont('Frederic', 'Frederic-Regular', 400);

@mixin ffrederic {
  font-family: 'Frederic', "Arial", sans-serif; }

@mixin famatic {
  font-family: 'Amatic', "Arial", sans-serif; }

body {
  @include famatic;
  font-size: 24px;
  line-height: 120%;
  font-weight: 400;
  background-color: $pale-lemon;
  @include smooth-font; }
